"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Header } from "@/components/header"
import { Sidebar } from "@/components/sidebar"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { DataTable } from "@/components/data-table"
import { Card } from "@/components/ui/card"
import { Plus } from "lucide-react"

const mockEntries = [
  { id: "1", supplier: "Tech Distributor", store: "Loja Centro", items: 5, total: "R$ 15.450", date: "2024-05-20" },
  {
    id: "2",
    supplier: "Eletrônicos Import",
    store: "Loja Vila Mariana",
    items: 8,
    total: "R$ 28.900",
    date: "2024-05-19",
  },
  { id: "3", supplier: "Global Supplies", store: "Loja Centro", items: 12, total: "R$ 45.600", date: "2024-05-18" },
]

export default function EntriesPage() {
  const router = useRouter()
  const [search, setSearch] = useState("")

  useEffect(() => {
    const token = localStorage.getItem("token")
    if (!token) {
      router.push("/login")
    }
  }, [router])

  const filteredEntries = mockEntries.filter(
    (entry) =>
      entry.supplier.toLowerCase().includes(search.toLowerCase()) ||
      entry.store.toLowerCase().includes(search.toLowerCase()),
  )

  const columns = [
    { key: "supplier" as const, label: "Fornecedor" },
    { key: "store" as const, label: "Loja de Destino" },
    { key: "items" as const, label: "Itens" },
    { key: "total" as const, label: "Total" },
    { key: "date" as const, label: "Data" },
  ]

  return (
    <div className="flex h-screen bg-background">
      <Sidebar />
      <div className="flex-1 flex flex-col overflow-hidden">
        <Header />
        <main className="flex-1 overflow-auto p-6">
          <div className="flex items-center justify-between mb-8">
            <div>
              <h1 className="text-3xl font-bold">Entradas de Produtos</h1>
              <p className="text-muted-foreground">Registre compras recebidas</p>
            </div>
            <Button className="gap-2">
              <Plus className="w-4 h-4" />
              Nova Entrada
            </Button>
          </div>

          <Card className="p-6 mb-6">
            <Input placeholder="Buscar entrada..." value={search} onChange={(e) => setSearch(e.target.value)} />
          </Card>

          <DataTable data={filteredEntries} columns={columns} />
        </main>
      </div>
    </div>
  )
}
